.react-responsive-modal-overlay {
    padding: 20px;
}

.react-responsive-modal-modal {
    height: calc(100% - 40px);
    max-width: 1400px !important;
    width: 100%;
}

.countryInfo {
    display: flex;
    flex-direction: column;
    height: 100%;
}

.countryInfo_title {
    font-size: 44px;
    text-align: center;
    font-weight: bold;
    margin-bottom: 16px;
}

.countryInfo_subtitle {
    font-size: 20px;
    text-align: center;
    font-weight: bold;
    margin-bottom: 16px;
}

.countryInfo_chart {
    display: flex;
    flex: 1;
    flex-direction: column;
}

.countryInfoChart_canvas {
    flex: 1;
}

.countryInfo_legend {
    display: flex;
    padding-top: 12px;
    flex-basis: 80px;
    justify-content: center;
}